



















































































































































































































































































import {Component, Vue} from 'vue-property-decorator';
import BaseFormLine from "@/components/form/BaseFormLine.vue";
import BaseFormItem from "@/components/form/BaseFormItem.vue";
import BaseForm from "@/components/form/BaseForm.vue";
import {trace_web} from '@/api';
import {ajaxDownload} from "@/util/apiUtil";
import m from 'moment';
import printTrace from './PrintTrace.vue';

@Component({
    name: 'Index',
    components: {
        BaseFormLine,
        BaseFormItem,
        BaseForm,
        printTrace,
    }
})
export default class Index extends Vue {
    query: any = {};
    info: any = {};
    nodeList: any[] = [];
    currNode: any = {};
    activeName: any = ['1', '2', '3', '4', '5', '6'];
    traceInfo: any = [];

    created() {
        let query = this.$route.query || {};
        if (query.traceCode) {
            this.$set(this.query, 'traceCode', query.traceCode);
            this.searchFun();
        }
    }

    downloadFun(item: any) {
        ajaxDownload('/file/download', {
            path: item.path || item.address,
            fileName: item.name
        })
    }

    getDate(date: any) {
        if (date == null || date === '') {
            return '-';
        }
        return m(date).format('YYYY年MM月DD日');
    }

    getIcon(item: any) {
        let suffix = item.suffix || '';
        let icon;
        let color;
        switch (suffix) {
            case 'jpg':
            case 'jpeg':
            case 'png':
            case 'gif':
                icon = 'uniE021';
                color = '#0076F6';
                break;
            case 'zip':
            case 'rar':
                icon = 'uniE022';
                color = '#5BB154';
                break;
            case 'pdf':
                icon = 'uniE055';
                color = '#DB0000';
                break;
            case 'doc':
            case 'docx':
                icon = 'uniE058';
                color = '#2D57CC';
                break;
            case 'xls':
            case 'xlsx':
                icon = 'uniE057';
                color = '#268C56';
                break;
            case 'ppt':
            case 'pptx':
                icon = 'uniE056';
                color = '#EF7431';
                break;
            default:
                icon = 'uniE024';
                color = '#AEAEAE';
                break;
        }
        return {
            icon: `icon-${icon}`,
            color: color
        }
    }

    selectFun(item: any) {
        if (this.currNode.id != item.id) {
            // 不能点击节点
            // this.currNode = item;
        }
    }

    searchFun() {
        trace_web.trace_web_traceCodeQuery_getByTraceCode_post({
            data: {
                traceCode: this.query.traceCode
            }
        }).then((data: any) => {
            let info = data.data || {};
            this.info = info;
            this.traceInfo = [{...info, traceCode: this.query.traceCode}];
            if (info.inflowType) {
                this.info.inflowType = info.inflowType.disName;
            }
            if (info.inspectionExemption) {
                this.info.inspectionExemption = info.inspectionExemption.disName;
            }
            if (info.origin) {
                this.info.isDomestic = info.origin.name === 'domestic';
                this.info.origin = info.origin.disName;
            }
            if (info.storageType) {
                this.info.storageType = info.storageType.disName;
            }
            this.nodeList = this.info.codeCirculationVos || [];
            //receiveName
            if (this.nodeList.length === 1) {
                let temp = this.nodeList[0] || {};
                temp.receiveName && this.nodeList.push({ownerName: temp.receiveName})
            }
            this.currNode = this.nodeList[0] || {};
        })

    }
}
