






import {Component, Prop, Vue} from 'vue-property-decorator';

@Component({
    name: "BaseLine"
})
export default class BaseLine extends Vue {
    @Prop({
        type: Object,
    })
    newStyle!: CSSStyleDeclaration | any

    @Prop({
        type: Boolean,
        default: false
    })
    noB!: boolean

    marginBottom = '';

    get style() {
        return {...{marginBottom: parseFloat(this.marginBottom) + 'px', ...this.newStyle}}
    }

    mounted() {

    }
}
