















































import {Component, Prop, Vue} from 'vue-property-decorator';
import {findComponentsDownward} from '@/util';
import _ from 'lodash';
import BaseFormLine from './BaseFormLine.vue';
import BaseFormItem from './BaseFormItem.vue';
import {Form} from "element-ui";


@Component({
    name: "BaseForm",
    components: {
        BaseFormLine,
        BaseFormItem
    }
})
export default class BaseForm extends Vue {
    public $refs!: {
        form: Form
    }

    @Prop({
        type: Number,
        default: 40
    })
    top!: number;

    @Prop({
        type: Number,
        default: 0
    })
    left!: number

    @Prop({
        type: Number,   //下边距离
        default: 50
    })
    bottom!: number

    @Prop({
        type: [Number,String],
        default: 0
    })
    marginBottom!: number|string

    @Prop({
        type: Boolean,
        default: false
    })
    showSaveBtn!: boolean

    @Prop({
        type: String,
        default: '保 存'
    })
    showSaveBtnName!: string

    @Prop({
        type: Boolean,
        default: false
    })
    showBackBtn!: boolean

    @Prop({
        type: String,
        default: '取 消'
    })
    showBackBtnName!: string

    @Prop({
        type: Boolean,
        default: false
    })
    showLine!: boolean

    @Prop({
        type: Boolean,
        default: false
    })
    bl!: boolean

    @Prop({
        type: Boolean,
        default: false
    })
    br!: boolean

    @Prop({
        type: Boolean,
        default: false
    })
    backAlert!: boolean

    @Prop({
        type: Boolean,
        default: true
    })
    isAdd!: boolean

    //按钮是否居中
    @Prop({
        type: Boolean,
        default: true
    })
    isBtnCenter!: boolean

    labelWidth = '0'

    validate(cb:Function) {
        if (this.$refs.form) {
            this.$refs.form.validate((valid) => {
                if (_.isFunction(cb)) {
                    cb(valid)
                } else {
                    console.error('入参为一个函数')
                }
            })
        } else {
            console.error('缺少ref为form的对象')
        }
    }

    back() {
        if (this.backAlert && this.showSaveBtn) {
            this.$confirm(this.isAdd ? `您页面数据还未保存，确定要返回吗？` : `当前页面更新数据尚未保存，是否确定返回？`, '提示',{
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$emit('on-back');
            })
        } else {
            this.$emit('on-back');
        }
    }

    mounted() {
        const baseLine = (findComponentsDownward(this, 'BaseLine') as BaseFormLine[]);
        _.each(baseLine, (item:any) => {
            item.marginBottom = this.marginBottom;
        });
        if (!this.isBtnCenter) {
            this.labelWidth = this.$refs.form.labelWidth
        }
    }
}
